.MainNav{
    background-color: rgb(50, 75, 56, 0.1);
    box-shadow: 0PC 1PX 2PX 1PX #888888;
    width: 100%;
    max-height: 60px;
    position: fixed;
}

.Navbar{
    width: 100%;
    display: flex;
    height: 60px;
    align-items: center;
    justify-content: space-around;
    max-width: 1200px;
}
.Navbar img{
    width: 120px;
}
.Navbar .NavLinks{
    list-style: none;
    display: flex;
    font-weight: 600;
}
.Navbar .NavLinks li{
    margin: 10px 10px;
    font-size: 17px;
    padding: 6px 12px;
    color: rgb(50, 75, 56);
    box-shadow: 0px 0px 2px 2px #666;
    border-radius: 16px;
}
.Navbar .NavLinks li:hover{
    color: white;
    cursor: pointer;
    background: rgb(50, 75, 56);
}
.Navbar .SearchBar{
    background: rgb(42, 87, 44, 0.2);
    height: 35px;
    border-radius: 15px;
    padding: 0px;
    width: 240px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0PC 0PX 2PX 2PX #777;
}
.Navbar .SearchBar input{
    background: none;
    border-width: 0px;
    height: 33px;
    width: 180px;
    outline: none;
    margin-left: 10px;
}
.Navbar .SearchBar input::-webkit-input-placeholder{
    color: white;
}
.Navbar .SearchBar input:-ms-input-placeholder{
    color: white;
}
.Navbar .SearchBar input::-ms-input-placeholder{
    color: white;
}
.Navbar .SearchBar input::placeholder{
    color: white;
}
.Navbar .SearchBar .searchIcon{
    font-size: 14px;
    margin: auto;
    background: rgb(42, 87, 44, 0.6);
    padding: 10px 5px;
    border-radius: 15px;
    color: white;
}
.Navbar button{
    display: none;
}
@media only screen and (max-width: 800px){
    .Navbar .SearchBar{
        display: none;
    }
    .Navbar button{
        display: inline;
        background: rgb(42, 87, 44, 0.2);
        border-width: 0px;
        padding: 10px 20px;
        box-shadow: 0Px 0PX 2PX 1PX #888888;
        border-radius: 2px;
        font-size: 16px;
        color:rgb(42, 87, 44);
        cursor: pointer;
        outline: none;
    }
    .Navbar button .menuIcon{
        margin-left: 10px;
    }

    .Mobile{
        display: flex;
        justify-content: flex-end;
    }
    .mobileLinks{
        justify-content: right;
        width: 100px;
        text-align: right;
        background-color: rgb(50, 75, 56, 0.1);
        box-shadow: -1Px 1PX 2PX 1PX #888888;
        margin-top: 0px;
        padding: 10px;
    }
    .mobileLinks li{
        list-style: none;
        margin-top: 10px;
        margin-bottom: 10px;
        font-size: 17px;
        padding: 4px 4px;
        text-align: center;
        color: rgb(50, 75, 56);
        box-shadow: 0px 0px 2px 2px #666;
        border-radius: 14px;
    }
    .mobileLinks li:hover{
        color: white;
        cursor: pointer;
        background: rgb(50, 75, 56);
    }
}
@media only screen and (max-width: 600px){
    .Navbar{
        display: flex;
        justify-content: space-between;
    }
    .Navbar img{
        margin-left: 20px;
    }
    .Navbar .NavLinks li{
        display: none;
    }
    .Navbar button{
        margin-right: 20px;
    }
}
